/* global*/

:root {
    --navHt    : 70px;
    --widthPage: 1100px;
    --purple   : #303F9F; 
    --red      : #be3434;
    --indigo   : #3a5ad5; 
    --lgPurple : #C5CAE9;
    --accent   : #7C4DFF; 
    --text     : #212121;
    --mGray    : #757575; 
    --sGray    : #BDBDBD; 
}


body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
     -moz-osx-font-smoothing: grayscale;
}
  
code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
}
body * {
      transition: 0.2s;
}


/* utils */

.flexcenter{
    display: flex;
    align-items:center;
    justify-content:center;
}


.page{
    margin-top: var(--navHt);
    overflow: hidden;
    position: relative;
    right: 0px;
    animation-name: moving;
    animation-duration: 0.6s;
}

.shadow{
    -webkit-box-shadow: 0px 4px 10px 1px #000000;
    box-shadow: 0px 4px 10px 1px #000000;
}


.column-container{
    display:grid;
    grid-template-columns: 50% 50%;
}

.column-container div{
    margin: 0 auto; 
}

.footer{
    background-color: var(--indigo);
    color: white;
    flex-direction: column;
    padding: 20px;
} 

.footer img{
    width: 30px;
}

.footer a{
    display: flex;
    justify-content: space-around;
    width: 200px;
    text-decoration: none;
    color: white;
    margin: 10px;
    align-items: center;
}


.oval-module{
    margin: 0 auto;
    width: var(--widthPage);
    max-width: 80%;
    padding: 50px 0;
    margin-top: 70px;
    margin-bottom: 90px;
    border-radius: 80px;
}

/* intro */


.intro-text{
    margin: 0 auto;
    display: flex;
    justify-content:center;
    flex-direction: column;
    width: 300px;
    max-width:80%;
    margin-bottom:800px;
}

.intro-text li{
    margin: 5px;
}

.intro-text h2{
    font-size: 40px;
    color: var(--indigo);
    margin-bottom: 5px;
}

.logo-b{
    margin-top: 40px;
    height: 450px;
    width: 370px;
}


/* content */

.content-container{
    background-color: var(--indigo);
    width: 100%;
    padding: 50px 0;
    color: white;
    overflow:hidden;
}

.paragraph{
    margin: 0 auto; 
    width: var(--widthPage);
    max-width: 80%;
    margin-top: 80px;
}

.paragraph p{
    margin: 0 auto;
    width: 350px;
    margin-top: 50px;
}

.paragraph img{
    width: 400px;
}

/* previous */

.portfolio{
    margin: 0 auto;
    margin-top: 70px;
    margin-bottom: 120px;
    width: var(--widthPage);
    max-width: 80%;
    justify-content: space-around;
    flex-direction: column;
}

.portfolio h2{
    color: var(--purple);
    margin-bottom: 80px;
}

.pagecard{
    flex-direction: column;
    width: 300px; 
    padding-bottom: 35px;
    color: var(--text);
    border: 1px solid var(--indigo);
    border-radius:5px;
    -webkit-box-shadow: 12px 9px 0px 1px var(--purple);
    box-shadow: 12px 9px 0px 1px var(--purple);
}

.pagecard img{
    width: 200px;
    height:200px;
}
.pagecard h3 {
    margin:40px;
}

.pagecard:hover{
    transform: scale(1.05);
    color: white;
    background-color: var(--indigo);
}

.project{
    position: fixed;
    top: var(--navHt);
    right: 0px;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction:column;
    color: white;
    background-color: var(--purple);
    overflow-y: scroll;
}

.hidden-pr{
    right: 100%;
}

.project-content{
    width: 600px;
    display:flex;
    flex-direction:column;
}
.project p, ul{
    align-self: center;
    width: 500px;
    max-width: 70%;
}

.project img{
    margin-top: 20px;
    width: 600px;
    margin-bottom: 80px;
}

.wrapper-exit{
    top: 80px;
    right: 40px;
    position: fixed;
}

.wrapper-exit-hd{
    top: 80px;
    right: 100%;
    position: fixed;
}
.

.wrapper-exit img:hover{
    transform: rotate(720deg);
    transition: 2s;
}
.wrapper-exit img{
    height: 40px;
    width: 40px;
}

.wrapper-exit-hd img{
    height: 40px;
    width: 40px;
}

/* Contact */

.form-cont{
    flex-direction: column;
    background-color: var(--indigo);
    color: white;
    margin-top: 40px;
    margin-bottom: 80px;
}

.input-field{
    margin: 20px;
    width: 400px;
    max-width: 80%;
    height: 30px;
    text-align: center;
    color: var(--purple);
    border: 3px solid white;
    border-radius: 5px;
}

.textarea{
    height: 120px;
}

.button-form{
    margin-top: 50px;
    height: 40px;
    width: 100px;
    color: white;
    border: 2px solid white;
    border-radius: 25px;
    background-color: var(--indigo);
}

.button-form:hover{
    color: var( --purple);
    background-color: white;
}

/* nav */



.navbar{
    position: fixed;
    top: 0;
    height: var(--navHt);
    width: 100%;
    background-color: var(--purple);
    color: white;
    display: grid;
    align-items:center;
    grid-template-columns: 50% 50%;
    z-index: 4;
}

.navbar img{
    margin-left: 5px;
    height: 90%;
    width: 60px;
}

.item-container{
    display: flex;
    align-items:center;
    flex-direction: row-reverse;
    margin-right: 10px;
}
.item-container button{
    border-radius: 5px;
    height:40px;
    width: 100px;
    margin: 0 18px 0 0;
    display:flex;
    align-items: center;
    border: none;
    font-size: 15px;
    background-color: var(--purple);
    justify-content: center;
    color: white;
}



.item-container button:hover{
    background-color: var(--indigo);
}

@keyframes moving {
  0%   {right: -100%;}
  100% {right: 0px;}
}

@media only screen and (max-width: 1000px) {
    .column-container {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .column-container div,p, img{
        margin-top: 30px;
    }
    .paragraph{
        margin-top: 20px;
    }

}
